@import url(https://fonts.google.com/specimen/Red+Hat+Display);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
.App {
  background-image: url(/order-summary-component/static/media/pattern-background-mobile.c8d50fcb.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-color: hsl(225, 100%, 94%);
  display: grid;
  justify-items: center;
  align-items: center;
  font-family: 'Red Hat Display', sans-serif;
  position: absolute;
  height: 100%;
  width: 100%;
}

@media screen and (min-width: 568px) {
  .App {
    background-image: url(/order-summary-component/static/media/pattern-background-desktop.5a0ab689.svg);
    padding: 5%;
  }
}
